import JSBI from 'jsbi'
import TokSwapConstants from "./tokswap_constants"
// exports for external consumption
export type BigintIsh = JSBI | bigint | string

export enum ChainId {
  MAINNET = 20,
  ROPSTEN = 3,
  RINKEBY = 4,
  GÖRLI = 5,
  KOVAN = 42,
  DEV = 1337,
  TESTNET = 21,
  HECO_MAINNET = 128,
  HECO_TESTNET = 256
}

export enum TradeType {
  EXACT_INPUT,
  EXACT_OUTPUT
}

export enum Rounding {
  ROUND_DOWN,
  ROUND_HALF_UP,
  ROUND_UP
}

//export const FACTORY_ADDRESS = '0x5C69bEe701ef814a2B6a3EDD4B1652CB9cc5aA6f'
export const FACTORY_ADDRESS = TokSwapConstants.MAINNET.UniswapV2Factory
// export const INIT_CODE_HASH = '0x96e8ac4277198ff8b6f785478aa9a39f403cb768dd02cbee326c3e7da348845f'
export const INIT_CODE_HASH = TokSwapConstants.MAINNET.INIT_CODE_HASH
export function getFactoryAddress(chainId: number | undefined) {
  switch(chainId) {
  case ChainId.DEV: {
    return TokSwapConstants.DEV.UniswapV2Factory
  }
  case ChainId.TESTNET: {
    return TokSwapConstants.TESTNET.UniswapV2Factory
  }
  case ChainId.MAINNET: {
    return TokSwapConstants.MAINNET.UniswapV2Factory
  }
  case ChainId.HECO_MAINNET: {
    return TokSwapConstants.HECO_MAINNET.UniswapV2Factory
  }
  case ChainId.HECO_TESTNET: {
    return TokSwapConstants.HECO_TESTNET.UniswapV2Factory
  }
  default: {
    return TokSwapConstants.MAINNET.UniswapV2Factory
  }
  }
}


export function getCodeHash(chainId: number | undefined) {
  switch(chainId) {
    case ChainId.DEV: {
      return TokSwapConstants.DEV.INIT_CODE_HASH
    }
    case ChainId.TESTNET: {
      return TokSwapConstants.TESTNET.INIT_CODE_HASH
    }
    case ChainId.MAINNET: {
      return TokSwapConstants.MAINNET.INIT_CODE_HASH
    }
    case ChainId.HECO_MAINNET: {
      return TokSwapConstants.HECO_MAINNET.INIT_CODE_HASH
    }
    case ChainId.HECO_TESTNET: {
      return TokSwapConstants.HECO_TESTNET.INIT_CODE_HASH
    }
    default: {
      return TokSwapConstants.MAINNET.INIT_CODE_HASH
    }
  }
}

